<template>
  <div>
    <!---->
    <div class="board">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!---->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="4">
          <el-select v-model="selectedHallId" placeholder="" clearable>
            <el-option
                v-for="item in hallList"
                :key="item.hallId"
                :label="item.hallName"
                :value="item.hallId">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-select v-model="selectedMovieId" placeholder="" clearable>
            <el-option
                v-for="item in movieList"
                :key="item.movieId"
                :label="item.movieName"
                :value="item.movieId">
            </el-option>
          </el-select>
        </el-col>

        <el-col :span="5">
          <el-date-picker
              v-model="selectedDate"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
          </el-date-picker>
        </el-col>

        <el-col :span="2">
          <el-button icon="el-icon-search" @click="getSessionList"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="showAddDialog"></el-button>
        </el-col>
        <el-col :span="2">
<!--          <el-button type="danger" @click="multipleDelete"></el-button>-->
          <el-button type="danger" @click="isAbleMultipleDelete"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="sessionList" style="width: 100%" border stripe @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="sessionId" label="#" width="40"></el-table-column>
        <el-table-column prop="sysHall.hallName" label=""></el-table-column>
        <el-table-column prop="sysMovie.movieName" label=""></el-table-column>
        <el-table-column prop="languageVersion" label=""></el-table-column>
        <el-table-column prop="sessionDate" label="" width="100"></el-table-column>
        <el-table-column prop="playTime" label="" width="110">
          <template slot-scope="scope">
            <span>{{scope.row.playTime}} - {{scope.row.endTime}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="sessionPrice" label=""></el-table-column>
        <el-table-column prop="seatNums" label=""></el-table-column>
        <el-table-column prop="sallNums" label=""></el-table-column>
        <el-table-column prop="seatNums - sallNums" label="">
          <template slot-scope="scope">
            <span>{{scope.row.seatNums - scope.row.sallNums}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="sessionTips" label=""></el-table-column>

        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="" placement="top" :enterable="false" :open-delay="500">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="isAbleEdit(scope.row.sessionId)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false" :open-delay="500">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="isAbleDelete(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false" :open-delay="500">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="checkSeat(scope.row.sessionId)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pageNum"
          :page-sizes="[5, 7, 9]"
          :page-size="queryInfo.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>

    </el-card>

    <!---->
    <el-dialog title="" :visible.sync="addDialogVisible" width="60%" @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="150px">
        <!--propaddFormRules v-model-->
        <el-form-item label="" prop="hallId">
          <el-select v-model="addForm.hallId" placeholder="" clearable >
            <el-option
                v-for="item in hallList"
                :key="item.hallId"
                :label="item.hallName"
                :value="item.hallId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="languageVersion">
          <el-input v-model="addForm.languageVersion"></el-input>
        </el-form-item>
        <el-form-item label="" prop="movieId">
          <el-select v-model="addForm.movieId" placeholder="" clearable >
            <el-option
                v-for="item in movieList"
                :key="item.movieId"
                :label="item.movieName"
                :value="item.movieId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="sessionDate">
          <el-date-picker
              v-model="addForm.sessionDate"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="sessionTime">
          <el-time-picker
            v-model="addForm.playTime"
            value-format = 'HH:mm'
            placeholder="">
          </el-time-picker>
        </el-form-item>
        <el-form-item label="" prop="sessionPrice">
          <el-input v-model="addForm.sessionPrice"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sessionTips">
          <el-input v-model="addForm.sessionTips"></el-input>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
      <el-button @click="addDialogVisible = false"> </el-button>
      <el-button type="primary" @click="addSession"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="editDialogVisible" width="60%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="150px">
        <el-form-item label="" prop="hallId">
          <el-select v-model="editForm.hallId" placeholder="" clearable >
            <el-option
                v-for="item in hallList"
                :key="item.hallId"
                :label="item.hallName"
                :value="item.hallId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="languageVersion">
          <el-input v-model="editForm.languageVersion"></el-input>
        </el-form-item>
        <el-form-item label="" prop="movieId">
          <el-select v-model="editForm.movieId" placeholder="" clearable >
            <el-option
                v-for="item in movieList"
                :key="item.movieId"
                :label="item.movieName"
                :value="item.movieId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="sessionDate">
          <el-date-picker
              v-model="editForm.sessionDate"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="sessionTime">
          <el-time-picker
            v-model="editForm.playTime"
            value-format = 'HH:mm'
            placeholder="">
          </el-time-picker>
        </el-form-item>
        <el-form-item label="" prop="sessionPrice">
          <el-input v-model="editForm.sessionPrice"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sessionTips">
          <el-input v-model="editForm.sessionTips"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editSessionInfo"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="checkDialogVisible" :width="checkDialogWidth">
      <div style="display:flex; align-content: center">
        <span style="margin-right: 10px"></span>
        <span class="seat" :class="isSelected[0]"></span>
        <span style="margin: 0 10px"></span>
        <span class="seat" :class="isSelected[1]"></span>
        <span style="margin: 0 10px"></span>
        <span class="seat" :class="isSelected[2]"></span>
      </div>
      <br/>
      <div class="row" v-for="(value, key) in seats">
        <span style="margin-right: 100px">{{key}}</span>
        <div style="display: flex; justify-content: flex-end">
          <span class="seat" :class="isSelected[seats[key][index]]" v-for="(item, index) in value"></span>
        </div>
      </div>
    </el-dialog>


  </div>
</template>

<script>
import global from "@/assets/css/global.css"
import echarts from 'echarts'
import moment from 'moment'
export default {
  name: "HallInfo",
  data() {
    return {
      queryInfo: {
        hallId: '',
        movieId: '',
        sessionDate: '',
        pageNum: 1,
        pageSize: 7
      },
      sessionList: [],
      total: 0,
      //
      addDialogVisible: false,
      //
      addForm: {
        hallId: '',
        languageVersion: '',
        movieId: '',
        playTime: '',
        endTime: '',
        deadline: '',
        sessionDate: '',
        sessionPrice: '',
        sessionTips: '',
        sessionSeats: '',
        seatNums: 0
      },
      //
      addFormRules: {
        hallId: [
          { required: true, message: '', trigger: 'change' }
        ],
        languageVersion: [
          { required: true, message: '', trigger: 'blur' }
        ],
        movieId: [
          { required: true, message: '', trigger: 'change' }
        ],
        sessionDate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        playTime: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sessionPrice: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editDialogVisible: false,
      editForm: {},
      checkAbleId: {},
      editFormRules: {
        hallId: [
          { required: true, message: '', trigger: 'change' }
        ],
        languageVersion: [
          { required: true, message: '', trigger: 'blur' }
        ],
        movieId: [
          { required: true, message: '', trigger: 'change' }
        ],
        sessionDate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        playTime: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sessionPrice: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      selectedHallId: '',
      endPlayTime: '',
      selectedMovieId: '',
      selectedDate: '',
      hallList: [],
      movieList: [],
      multipleSelection: [],
      checkDialogVisible: false,
      seats: {},
      col: '',
      isSelected: {
        0: 'seat-default',
        1: 'seat-choose',
        3: 'seat-sold'
      },
      editSeat: {},
      checkDialogWidth: '',
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      rowNums: '',
      colNums: '',
      rowStart: '',
      loginUser: JSON.parse(window.sessionStorage.getItem('loginUser'))
    }
  },
  created() {
    this.getSessionList()
    this.getHallList()
    this.getMovieList()
    // this.curCinemaName = this.loginUser.cinemaName
  },
  methods: {
    getSessionList() {
      this.queryInfo.hallId = this.selectedHallId
      this.queryInfo.movieId = this.selectedMovieId
      this.queryInfo.sessionDate = this.selectedDate
      console.log(String(this.selectedDate))
      const _this = this;
      axios.get('sysSession', {params: _this.queryInfo}).then(resp => {
        console.log(resp)
        _this.sessionList = resp.data.data;
        _this.total = resp.data.total;
        _this.queryInfo.pageSize = resp.data.pageSize;
        _this.queryInfo.pageNum = resp.data.pageNum;
      })
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getSessionList()
      console.log(newSize)
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getSessionList()
      console.log(newPage)
    },
    showAddDialog(){
      this.addDialogVisible = true
    },
    // 
    addDialogClosed(){
      this.$refs.addFormRef.resetFields()
    },
    // 
    addSession(){
      console.log('')
      console.log(this.addForm)
      const _this = this
      // 
      this.handleEndTimeAndDeadline(_this.addForm.movieId, _this.addForm.sessionDate, _this.addForm.playTime)
      console.log('function' + _this.addForm.endTime)
      this.$refs.addFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return this.$message.error('')
        //id

        let seats = ''
        let seatNums = 0
        //sessionSeats
        await axios.get('sysHall/' + this.addForm.hallId).then(resp => {
          _this.rowNums = resp.data.data.rowNums
          _this.colNums = resp.data.data.seatNumsRow
          _this.rowStart = resp.data.data.rowStart
          seatNums = resp.data.data.seatNums
          seats = resp.data.data.seatState
        })

        // let row = this.rowNums
        // let col = this.colNums
        // let seat = {}
        // for (let i = 0; i < row; i++) {
        //   let arr = []
        //   for (let j = 0; j < col; j++) {
        //     arr.push(0)
        //   }
        //   seat[isNaN(parseInt(_this.rowStart)) ? String.fromCharCode(i + _this.rowStart.charCodeAt(0)) : i + parseInt(_this.rowStart)] = arr
        // }
        // this.$set(this.addForm, 'sessionSeats', JSON.stringify(seat))
        //
        this.$set(this.addForm, 'seatNums', seatNums)
        this.$set(this.addForm, 'sessionSeats', seats)
        console.log(_this.addForm.sessionSeats)

        // 
        axios.defaults.headers.post['Content-Type'] = 'application/json'
        await axios.post('sysSession', JSON.stringify(_this.addForm)).then(resp => {
          console.log(resp)
          if (resp.data.code !== 200){
            this.$message.error('')
          }
        })

        //
        this.addDialogVisible = false
        //
        this.getSessionList()
        this.$message.success('')
      })
    },
    // 
    isAbleEdit(id) {
      this.checkAbleId.sessionId = id
      axios.get('sysBill', {params: this.checkAbleId}).then(response => {
        console.log(response.data.total)
        console.log('data')
        console.log(response.data)
        console.log('data.data')
        console.log(response.data.data)
        console.log(response.data.data.length)
        let bills = response.data.data
        if (response.data.total === 0) {
          console.log('')
          this.showEditDialog(id)
        } else {
          console.log('')
          let billIds = ''
          for (let temp of bills) {
            billIds += temp.billId+' '
          }
          console.log('billIds is : '+billIds)
          this.$alert('\n'+': '+billIds, '', {
            confirmButtonText: '',
            callback: action => {
              this.$router.push('/session')
            }
          })
        }
      })
    },
    // 
    async showEditDialog(id){
      const _this = this
      const { data : res} = await axios.get('sysSession/find/' + id)
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 
    editDialogClosed(){
      this.$refs.editFormRef.resetFields()
    },
    // 
    editSessionInfo(){
      this.handleEndTimeAndDeadline(this.editForm.movieId, this.editForm.sessionDate, this.editForm.playTime)
      let row = this.editForm.rowNums
      let col = this.editForm.seatNumsRow
      this.$set(this.editForm, 'seatNums', row * col)
      let seat = {}
      for (let i = 0; i < row; i++) {
        let arr = []
        for (let j = 0; j < col; j++) {
          arr.push(0)
        }
        seat[isNaN(parseInt(this.editForm.rowStart)) ? String.fromCharCode(i + this.editForm.rowStart.charCodeAt(0)) : i + parseInt(this.editForm.rowStart)] = arr
      }
      this.$set(this.editForm, 'seatState', JSON.stringify(seat))

      this.$refs.editFormRef.validate(async valid => {
        const _this = this
        if (!valid) return
        let success = true
        axios.defaults.headers.put['Content-Type'] = 'application/json'
        await axios.put('sysSession', JSON.stringify(_this.editForm)).then(resp => {
          if (resp.data.code !== 200){
            this.$message.error('')
            success = false
          }
        })
        if (!success) return
        this.editDialogVisible = false
        this.getSessionList()
        this.$message.success('')
      })
    },
    //
    handleEndTimeAndDeadline(movieId, sessionDate, playTime){
      this.addForm.deadline = ''
      this.editForm.deadline = ''
      this.addForm.endTime = ''
      this.editForm.endTime = ''
      let resultEndTime = ''
      //
      let movieLengthTime
      for (let item of this.movieList) {
        if (item.movieId === movieId) {
          movieLengthTime = item.movieLength
          break
        }
      }
      //moment
      let tempTime = new Date(sessionDate)
      //
      let hoursMin = playTime.split(':')
      tempTime.setHours(hoursMin[0])
      tempTime.setMinutes(hoursMin[1])
      tempTime.setSeconds(0)
      let tempEndTime = moment(tempTime).add(movieLengthTime,'m')._d
      // 20
      let tempDeadline = moment(moment(tempEndTime).add(20,'m')._d).format('YYYY-MM-DD HH:mm:ss')

      // deadline
      this.addForm.deadline = tempDeadline
      this.editForm.deadline = tempDeadline
      console.log(''+tempEndTime);
      let endHours = tempEndTime.getHours()
      let endMins = tempEndTime.getMinutes()
      //
      if (endHours < 10) {
        resultEndTime = '0'+endHours+':'
      } else {
        resultEndTime = endHours+':'
      }
      if (endMins < 10) {
        resultEndTime += '0'+endMins
      } else {
        resultEndTime += endMins
      }
      console.log('' + resultEndTime)
      // return resultEndTime
      this.addForm.endTime = resultEndTime
      this.editForm.endTime = resultEndTime
    },
    // 
    handleSelectionChange(val){
      this.multipleSelection = val
    },
    // 
    async isAbleMultipleDelete() {
      let ableMultipleDelete = true
      let ids = ''
      for (let i = 0; i < this.multipleSelection.length; i++) {
        this.checkAbleId.sessionId = this.multipleSelection[i].sessionId

        console.log('sessionid' + this.checkAbleId.sessionId)
        // 
        // const { data : resp } = await axios.get('sysSession', {params: this.checkAbleId})
        const { data : resp } = await axios.get('sysSession/find/' + this.checkAbleId.sessionId)
        console.log('')
        console.log(resp.data)
        let curSessionInfo = resp.data
        // 
        if (curSessionInfo.deadline <= moment(new Date()).format('YYYY-MM-DD HH:mm:ss')) continue

        const { data : res } = await axios.get('sysBill', {params: this.checkAbleId})
        if (res.data.length !== 0) {
          ids += this.multipleSelection[i].sessionId + ' '
          ableMultipleDelete = false
        }
      }
      if (ableMultipleDelete === false) {
        // '\\n\'+\': \''
        this.$alert('\n' + ': ' + ids, '', {
          confirmButtonText: '',
          callback: action => {
            this.$router.push('/session')
          }
        })
        return
      }
      await this.multipleDelete()
    },
    async multipleDelete(){
      const _this = this
      //
      const resp = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // , resp"confirm"
      // resp"cancel"
      if (resp === 'cancel'){
        return _this.$message.info('')
      }

      let ids = []
      this.multipleSelection.forEach(data => {
        ids.push(data.sessionId)
      })
      console.log(ids)
      await axios.delete('sysSession/' + ids).then(resp => {
        if (resp.data.code !== 200){
          this.$message.success('')
        }
      })
      this.getSessionList()
      this.$message.success('')
    },
    // 
    async isAbleDelete(session) {
      if (session.deadline <= moment(new Date()).format('YYYY-MM-DD HH:mm:ss')) {
        await this.deleteSessionById(session.sessionId)
        return
      }
      this.checkAbleId.sessionId = session.sessionId
      axios.get('sysBill', {params: this.checkAbleId}).then(response => {
        console.log(response.data.total)
        let bills = response.data.data
        if (response.data.total === 0) {
          console.log('')
          this.deleteSessionById(session.sessionId)
        } else {
          console.log('')
          let billIds = ''
          for (let temp of bills) {
            console.log(temp)
            billIds += temp.billId+' '
          }
          console.log('billIds is : '+billIds)
          this.$alert('\n'+': '+billIds, '', {
            confirmButtonText: '',
            callback: action => {
              this.$router.push('/session')
            }
          })
        }
      })
    },
    //id
    async deleteSessionById(id){
      const _this = this
      //
      const resp = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // , resp"confirm"
      // resp"cancel"
      console.log(resp)
      if (resp === 'cancel'){
        return _this.$message.info('')
      }

      await axios.delete('sysSession/' + id).then(resp => {
        if (resp.data.code !== 200){
          _this.$message.success('')
        }
      })
      this.getSessionList()
      this.$message.success('')
    },
    getHallList(){
      const _this = this
      axios.get('sysHall').then(resp => {
        _this.hallList = resp.data.data;
      })
    },
    getMovieList(){
      const _this = this
      axios.get('sysMovie/find').then(resp => {
        _this.movieList = resp.data.data;
      })
    },
    async checkSeat(id){
      const _this = this
      await axios.get('sysSession/find/' + id).then(resp => {
        console.log(resp)
        _this.seats = JSON.parse(resp.data.data.sessionSeats)
        _this.col = resp.data.data.sysHall.seatNumsRow
      })
      console.log(this.col)
      this.checkDialogWidth = 200 + this.col * 40 + 'px'
      this.checkDialogVisible = true
    }
  }
}
</script>

<style scoped>
.row{
  white-space: nowrap;
  margin-top: 10px;
  padding: 0 10px;
  text-align: center;
  display: flex;
  justify-content: space-between;
}
.seat{
  display: inline-block;
  width: 30px;
  height: 26px;
  margin: 0 5px;
  background-position: 0 1px;
}
.seat-default {
  background: url("data:image/png;base64,") no-repeat;
}
.seat-choose{
  background: url("data:image/png;base64,") no-repeat;
}
.seat-sold{
  background: url("data:image/png;base64,") no-repeat;
}
</style>
