    <!---->
    <el-dialog title="" :visible.sync="addDialogVisible" width="60%" @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <!--propaddFormRules v-model-->
        <el-form-item label="" prop="movieName">
          <el-input v-model="addForm.movieName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="movieArea">
          <el-select v-model="addForm.movieArea" placeholder="" clearable >
            <el-option
                    v-for="item in movieAreaList"
                    :key="item"
