                    :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="/" prop="movieLength">
          <el-input v-model="addForm.movieLength"></el-input>
        </el-form-item>
        <el-form-item label="" prop="releaseDate">
          <el-date-picker
              v-model="addForm.releaseDate"
              value-format="yyyy-MM-dd HH:mm:ss"
