<template>
  <div>
    <!---->
    <div class="board">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!---->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="5">
          <el-input v-model="inputUserName" placeholder="" clearable></el-input>
        </el-col>
        <el-col :span="4">
          <el-select v-model="selectedState" placeholder="" clearable>
            <el-option v-for="item in payStates" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-col>

        <el-col :span="5">
          <el-date-picker
              v-model="selectedDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="">
          </el-date-picker>
        </el-col>

        <el-col :span="2">
          <el-button icon="el-icon-search" @click="getBillList"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="danger" @click="multipleDelete"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="billList" style="width: 100%" border stripe @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="billId" label="#" width="40"></el-table-column>
        <el-table-column prop="sysUser.userName" label="" width="80px"></el-table-column>
        <el-table-column prop="sysSession.sysMovie.movieName" label=""></el-table-column>
        <el-table-column prop="sysSession.sysHall.hallName" label=""></el-table-column>
        <el-table-column prop="sysSession.sessionDate" label=""></el-table-column>
        <el-table-column prop="sysSession.playTime, sysSession.endTime" label="">
          <template slot-scope="scope"> {{scope.row.sysSession.playTime}}-{{scope.row.sysSession.endTime}} </template>
        </el-table-column>
        <el-table-column prop="seats" label=""></el-table-column>
        <el-table-column prop="createTime" label=""></el-table-column>
        <el-table-column prop="payState" label="" width="80">
          <template slot-scope="scope">
            <span v-if="scope.row.payState === true" style="color: #13ce66"></span>
            <span v-if="scope.row.payState !== true && scope.row.cancelState !== true " style="color: #145ddc"></span>
            <span v-if="scope.row.cancelState === true && scope.row.cancelTime ===null" style="color: #e6a23c"></span>
            <span v-if="scope.row.cancelState === true && scope.row.cancelTime !==null" style="color: crimson"></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="" placement="top" :enterable="false" :open-delay="500">
              <el-button type="primary" icon="el-icon-edit" size="mini"
                         @click="showEditDialog(scope.row.billId)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false" :open-delay="500">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteBillById(scope.row.billId)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pageNum"
          :page-sizes="[5, 7, 9]"
          :page-size="queryInfo.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>



    <!---->
    <el-dialog title="" :visible.sync="editDialogVisible" width="60%" @close="editDialogClosed">
      <el-form :model="editForm" ref="editFormRef" label-width="100px">
        <!--propaddFormRules v-model-->
        <el-form-item label="" prop="billId">
          <el-input v-model="editForm.billId" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysUser.userName">
          <el-input v-model="editForm.sysUser.userName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysSession.sysMovie.movieName">
          <el-input v-model="editForm.sysSession.sysMovie.movieName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysSession.sysHall.hallName">
          <el-input v-model="editForm.sysSession.sysHall.hallName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysSession.sysHall.hallName">
          <el-input v-model="editForm.seats" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysSession.sessionDate">
          <el-input v-model="editForm.sysSession.sessionDate" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysSession.sessionDate">
          <el-input v-model="editForm.sysSession.playTime" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysSession.sessionDate">
          <el-input v-model="editForm.sysSession.endTime" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sysSession.sessionDate">
          <el-input v-model="editForm.createTime" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="payState" v-if="!editForm.cancelState">
          <el-select v-model="editForm.payState" placeholder="" clearable >
            <el-option
              v-for="item in payStates"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="cancelState" v-if="!editForm.payState">
          <el-select v-model="editForm.cancelState" placeholder="" clearable >
            <el-option
              v-for="item in cancelStates"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editBillInfo"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import global from "@/assets/css/global.css"
import moment from 'moment'
export default {
  name: "billInfo",
  data() {
    return {
      queryInfo: {
        queryByUserName: '',
        payState: '',
        createTime: '',
        pageNum: 1,
        pageSize: 7
      },
      billList: [],
      total: 0,
      multipleSelection: [],
      inputUserName: '',
      selectedState: '',
      selectedDate: '',
      editDialogVisible: false,
      editForm: {
        sysUser: {
          userName: ''
        },
        sysSession: {
          sysMovie: {},
          sysHall: {}
        }
      },
      payStates: [
        {
          id: 0,
          name: ''
        },
        {
          id: 1,
          name: ''
        }
      ],
      cancelStates: [
        {
          id: 0,
          name: ''
        },
        {
          id: 1,
          name: ''
        }
      ],
    }
  },
  created() {
    this.getBillList()
  },
  methods: {
    async getBillList() {
      // this.userName = this.inputUserName
      this.queryInfo.payState = this.selectedState
      this.queryInfo.createTime = this.selectedDate
      console.log('quaryInfo'+this.inputUserName)
      this.queryInfo.queryByUserName = this.inputUserName
      console.log('sysUser.userName')
      console.log(this.queryInfo.queryByUserName)
      const _this = this;
      await axios.get('sysBill', {params: _this.queryInfo}).then(resp => {
        console.log(resp)
        _this.billList = resp.data.data;
        _this.total = resp.data.total;
        _this.queryInfo.pageSize = resp.data.pageSize;
        _this.queryInfo.pageNum = resp.data.pageNum;
      })
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getBillList()
      console.log(newSize)
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getBillList()
      console.log(newPage)
    },
    // 
    handleSelectionChange(val){
      this.multipleSelection = val
    },
    // 
    async showEditDialog(id) {
      let isAbleEdit = true
      await axios.get('sysBill/' + id ).then(resp => {
        console.log(resp)
        this.editForm = resp.data.data
        this.editForm.sysUser = resp.data.data.sysUser
        console.log('this.editForm')
        console.log(this.editForm)

        if (this.editForm.payState === true || this.editForm.cancelState === true) {
          isAbleEdit = false
          this.$alert('', '', {
            confirmButtonText: '',
            callback: action => {
              this.$router.push('/bill')
            }
          })
        }
      })
      if (isAbleEdit) {
        this.editForm.payState = 0
        this.editForm.cancelState = 0
        this.editDialogVisible = true
      }
    },
    // 
    editDialogClosed(){
      this.$refs.editFormRef.resetFields()
    },
    // 
    async editBillInfo() {
      const _this = this
      if (_this.editForm.cancelState && _this.editForm.payState) {
        this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.$router.push('/bill')
          }
        })
        this.editDialogVisible = false
        return
      }
      if (_this.editForm.payState) {
        const { data: res} = await axios.put('sysBill', JSON.stringify(_this.editForm))
        if(res.code !== 200) return this.$message.error('')
        this.$message.success('')
        this.editDialogVisible = false
        this.getBillList()
        return
      } else if (_this.editForm.cancelState) {
        _this.editForm.cancelState = true
        _this.editForm.cancelTime = moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
        // 
        const { data : curSession } = await axios.get('sysSession/find/' + _this.editForm.sessionId)
        let sessionSeats = JSON.parse(curSession.data.sessionSeats)
        // 
        for(let seat of JSON.parse(_this.editForm.sests)){
          let row = seat.substring(0, seat.indexOf(''))
          let col = Number.parseInt(seat.substring(seat.indexOf('') + 1, seat.length - 1))
          sessionSeats[row][col - 1] = 0
        }
        // 
        axios.defaults.headers.put['Content-Type'] = 'application/json'
        const { data: resp } = await axios.put('sysBill/cancel',JSON.stringify({sysBill: _this.editForm, sessionSeats: JSON.stringify(sessionSeats)}))
        if(resp.code !== 200) return this.$message.error('')
        this.getBillList()
        this.editDialogVisible = false
        this.$message.success('')
      }
    },

    async multipleDelete(){
      const _this = this
      //
      const resp = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // , resp"confirm"
      // resp"cancel"
      if (resp === 'cancel'){
        return _this.$message.info('')
      }

      let ids = []
      this.multipleSelection.forEach(data => {
        ids.push(data.billId)
      })
      await axios.delete('sysBill/' + ids).then(resp => {
        if (resp.data.code !== 200){
          this.$message.success('')
        }
      })
      this.getBillList()
      this.$message.success('')
    },
    //id
    async deleteBillById(id){
      const _this = this
      //
      const resp = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // , resp"confirm"
      // resp"cancel"
      console.log(resp)
      if (resp === 'cancel'){
        return _this.$message.info('')
      }

      await axios.delete('sysBill/' + id).then(resp => {
        if (resp.data.code !== 200){
          _this.$message.success('')
        }
      })
      this.getBillList()
      this.$message.success('')
    }
  }
}
</script>

