<template>
  <div class="login_container">
    <div class="login_box">
      <div class="title_box">
        <p></p>
      </div>
      <!--  -->
      <el-form class="login_form" :model="loginForm" :rules="loginFormRules" ref="loginFormRef">
        <!--  -->
        <el-form-item prop="userName">
          <el-input v-model="loginForm.userName" placeholder="" clearable
                    prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" type="password" placeholder="" show-password
                    prefix-icon="iconfont icon-lock"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button size="medium" class="btn" :round="true" type="primary" @click="login"></el-button>
          <el-button size="medium" class="btn" :round="true" type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      //
      loginForm: {
        userName: 'admin',
        password: '123456'
      },
      //
      loginFormRules: {
        //
        userName: [
          { required: true, message: "", trigger: "blur"},
          { min:2, max: 20, message: "220", trigger: "blur"}
        ],
        //
        password: [
          { required: true, message: "", trigger: "blur"},
          { min:6, max: 16, message: "616", trigger: "blur"}
        ]
      }
    }
  },
  methods:{
    success(params) {
      console.log(params);
      this.login()
    },
    //
    resetLoginForm(){
      console.log(this.$refs)
      this.$refs.loginFormRef.resetFields();
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if(!valid) return;
        axios.defaults.headers.post['Content-Type'] = 'application/json'
        const { data: res} = await axios.post('sysUser/login', JSON.stringify(this.loginForm));
        if(res.code !== 200) return this.$message.error(res.msg);
        //
        if(res.data.sysUser.sysRole.children === null || res.data.sysUser.sysRole.children[0] === null) {
          this.$message.error("")
          return
        }
        this.$message.success("")
        // console.log(res.data);
        //token
        window.sessionStorage.setItem("token", res.data.token)
        window.sessionStorage.setItem("loginUser", JSON.stringify({sysUser : res.data.sysUser, cinemaId : res.data.cinemaId, cinemaName : res.data.cinemaName}));
        // window.sessionStorage.setItem("btnPermission", res.data.sysUser.sysRole.roleId === 1 ? "admin" : "normal")
        window.sessionStorage.setItem("btnPermission", res.data.sysUser.sysRole.roleId === 1 ? "admin" : "admin")
        //
        await this.$router.push('/welcome');
      })
    }
  }
}
</script>

